.button-purple-default {
  background: linear-gradient(90deg, #8d4dfe 0%, #9d6be8 90%);
  border-radius: 500px;
  border-style: none;
  color: #FFFFFF;
  font: Bold 0.76rem Roboto, sans-serif;
  height: 40px; }

.button-purple-small-width {
  width: 162px; }

.button-purple-medium-width {
  width: 222px; }

.button-purple-large-width {
  width: 276px; }

.button-withe-default {
  background: #FFFFFF;
  border-radius: 500px;
  border-style: none;
  color: #9D6BE8;
  font: Bold 0.76rem Roboto, sans-serif;
  border: 1px solid #9D6BE8;
  width: 288px;
  height: 40px; }

#card {
  background-color: #ffff; }
  #card p:nth-child(1) {
    color: #9D6BE8;
    font-size: 1.4rem; }
  #card p:nth-child(2n) {
    color: #2F1A5A;
    font-size: 1rem; }

p, h6, h3 {
  font-family: Roboto,sans-serif; }

#background-image {
  z-index: 3; }

#sub-block1 {
  z-index: 4;
  position: absolute; }
  #sub-block1 #sub-block-text {
    margin-top: 20%; }

#mountain-image {
  z-index: 2;
  position: absolute; }

#purpleWave-image-reverse {
  z-index: 2;
  top: -100px;
  position: relative;
  transform: rotate(-180deg); }

#purpleWave-image {
  width: 100%;
  position: relative;
  top: -18px; }

html {
  scroll-behavior: smooth; }

body {
  margin: 0;
  padding: 0;
  font-size: 16px;
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

#block-1 {
  width: 100%;
  overflow: hidden; }

#block-2 {
  overflow: hidden;
  background-color: #2F1A5A; }
  #block-2 p {
    font-size: .7rem; }
